@import "global.scss";

.section7_client-list{
  @include list;
  margin-right: -300px;
  margin-top: -300px;
  position: relative;
}

.section7_list-marker{
  position: absolute;
  color: $second-color;
  left: -55px;
  top: 14px;
  transition: all ease 250ms;
}

.section7_client-info{
  max-width: 530px;
}

.section7_client-top-info{
  display: flex;
  align-items: flex-end;
  &>img{
    width: 180px;
    height: 180px;
    margin-right: 40px;
  }
}

.section7_client-name{
  font-size: 30px;
  margin-bottom: 10px;
}

.section7_client-age{
  font-size: 18px;
  margin-bottom: 0;
}

.section7_client-review{
  margin-top: 30px;
  font-size: 18px;
  line-height: 30px;
  &>span{
    display: block;
    height: 15px;
  }
}