@import "global.scss";

.section9_map{
  width: 55%;
  height: inherit;
}

.section9_superscription-container>span, .section9_email-container>span{
  display: block;
  font-size: 14px;
  margin-top: 25px;
  margin-bottom: 15px;
}

.section9_superscription-container>p, .section9_email-container>p{
  font-size: 18px;
  margin: 0;
}

.section9_markers{
  margin-top: 30px;
}

.section9_markers-container{
  display: flex;
  align-items: center;
  font-size: 14px;
}

.section9_markers-container span{
  display: inline-block;
  width: 30px;
  height: 30px;
  margin-right: 10px;
  border-radius: 50%;
}

.section9_markers-circle{
  background: url("../../img/location.png") no-repeat center, $main-gradient;
}

.section9_markers-envelope{
  background: url("../../img/envelope.png") no-repeat center, $main-gradient;
}

.section9_law{
  font-size: 14px;
  margin-top: 30px;
  margin-bottom: 40px;
}